.faded {
  position: relative;
  display: inline-block;
  color: rgb(255, 255, 255);
  /* opacity: 0.2; */
}
.faded:after {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-collapse: separate;
}

