    box-shadow: inset 0 40px 40px -16px;
  }







  .timer {
    margin-top: 10px;
}
  
.timer > svg {
    width: 200px;
    height: 200px;
}

.timer > svg > circle {
    fill: none;
    stroke-opacity: 0.3;
    stroke: #0d6efd;
    strokeWidth: 10;
    transform-origin: center center;
    transform: rotate(-90deg);
}

.timer > svg > circle + circle {
    stroke-dasharray: 1;
    stroke-dashoffset: 1;
    strokeLinecap: round;
    stroke-opacity: 1;
}

.timer.animatable > svg > circle + circle {
    transition: stroke-dashoffset 0.3s ease;
}

.timer > svg > text {
    font-size: 2rem;
}

.timer > svg > text + text {
    font-size: 1rem;
}









  #ol.stepper {
    --default-b: lightgrey;
    --default-c: black;
    --active-b: #06b49d;
    --active-c: white;
    --circle: 2em; /* size of circle */
    --b: 1px; /* line thickness */
    
    display: flex;
    list-style: none;
    justify-content: space-between;
    background: 
      linear-gradient(var(--default-b) 0 0) no-repeat
      50% calc((var(--circle) - var(--b))/2)/100% var(--b);
    counter-reset: step;
    margin: 20px;
    padding: 0;
    font-size: 13px;
    /* font-weight: bold; */
    counter-reset: step;
    overflow: hidden;
  }
  #ol.stepper li {
    
    display: grid;
    place-items: center;
    gap: 5px;
    font-family: sans-serif;
    position: relative;
  }
  #ol.stepper li::before {
    content: counter(step) " ";
    font-size: 18px;
    counter-increment: step;
    display: grid;
    place-content: center;
    aspect-ratio: 1;
    height: var(--circle);
    border: 5px solid #fff;
    box-sizing: border-box;
    background: var(--active-b);
    color: var(--active-c);
    border-radius: 50%;
    font-family: monospace;
    z-index: 1;
  }
  #ol.stepper li.active ~ li::before{
    background: var(--default-b);
    color: var(--default-c);
  }
  #ol.stepper li.active::after {
    content: "";
    position: absolute;
    height: var(--b);
    right: 100%;
    top: calc((var(--circle) - var(--b))/2);
    width: 100vw;
    background: var(--active-b);
  }








  



